import Header from "../../components/Header.vue";
import Footer from "../../components/Footer.vue";
import Condividi from "../../components/Condividi.vue";
import DataHeader from "../../components/DataHeader.vue";
import { StringRenderer } from "../../../commons/js/string_renderer.js";
import { Pagina } from "../../../commons/js/pagina.js";
import { ConsiglioInforma } from "../../js/consiglioinforma";
import { Utils } from "../../../commons/js/utils";

export default {
    name: "app",
    components: {
        Footer,
        Header,
        Condividi,
        DataHeader
    },
    data: function () {
        $(document).ready(renderRivista);
        return {};
    }
};

function renderRivista() {
    var urlParams = new URLSearchParams(window.location.search);
    const idOggetto = urlParams.get('id');
    renderContenuto();

    function renderContenuto() {
        var urlSezione = apibase.baseurl + '10530/' + idOggetto;
        return $.ajax({
            url: urlSezione,
            success: function (rivista) {
                if (!rivista) {
                    window.location = process.env.BASE_URL + "notFound.html";
                }
                renderBreadcrumb(rivista);
                $("#title").text(rivista.title);
                var data = StringRenderer.renderDataTestuale(rivista.properties.data_presentazione);
                $("#subtitle").text(data);
                //$("#container_base").append(renderImmagine(rivista));
                rivista.components.capitoli.components.forEach(function (capitolo) {
                    $("#container_base").append(renderCapitolo(capitolo));
                })
                $("#container_base").append(Pagina.renderAllegati(rivista));
                $("#container_base").append(Pagina.renderLink(rivista));
                renderImmagine(rivista);
                renderDocumentiPdf(rivista);
                renderDocumentiAudio(rivista);
            }, error: function (res_err) {
                window.location = process.env.BASE_URL + "notFound.html";
            }
        });
    }

    function renderBreadcrumb(rivista) {
        const tipologia = rivista.properties.tipologia;
        let tipo, label;
        for (const entry of Object.entries(ConsiglioInforma.Riviste)) {
            if (entry[1].enum == tipologia) {
                tipo = entry[0];
                label = entry[1].label;
                break;
            }
        }
        let bcSezione = '<li class="breadcrumb-item">\
                            <a href="anni-rivista.html?rivista=' + tipo + '">' + label + '</a>\
                        </li>';
        $('#bread').append(bcSezione);
    }

    function renderCapitolo(capitolo) {
        return '<div>'
            + '<a class="d-block h6" href="' + apibase.baseurl + capitolo.properties.address + '" target="_blank">' + capitolo.title + '</a>'
            + (capitolo.properties.sottotitolo ? '<small class="d-block">' + capitolo.properties.sottotitolo + '</small>' : '')
            + (capitolo.properties.autore ? '<small class="d-block">' + capitolo.properties.autore + '</small>' : '')
            + (capitolo.properties.descrizione ? '<p>' + capitolo.properties.descrizione + '</p>' : '')
            + '<hr />'
            + '</div > ';
    }

    function renderImmagine(rivista) {
        let htmlImg = '';
        if (rivista.components.immagine) {
            var immagine, address, imgSrc = '';
            if (rivista.components.immagine.components.length > 0) {
                immagine = rivista.components.immagine.components[0];
                address = immagine.components.file.components[0].properties.address;
                imgSrc = baseImgUrl + address;
            }
            imgSrc = Utils.creaPlaceholder(imgSrc, '//via.placeholder.com/320x512');
            htmlImg = '<img class="img-fluid" src="' + imgSrc + '" />'
        }
        $('#immagineRivista').html(htmlImg);
    }

    function renderDocumentiPdf(rivista) {
        if (rivista.components.documenti) {
            let docs = [];
            rivista.components.documenti.components.forEach(function (documento) {
                docs.push(renderDocumento(documento));
            });
            $('#documenti').append(docs.join(''));
        }
    }

    function renderDocumentiAudio(rivista) {
        if (rivista.components.documenti_audio) {
            let docs = [];
            rivista.components.documenti_audio.components.forEach(function (documento) {
                docs.push(renderDocumento(documento));
            });
            $('#documenti').append(docs.join(''));
        }
    }

    function renderDocumento(documento) {
        let link = baseImgUrl + documento.properties.address;
        let nomeFile = documento.id + '.' + documento.properties.extension;
        return '\
                <div>\
                    <a href="' + link + '" target="_blank" download=" ' + nomeFile + '" class="btn btn-default">\
                        <svg class="icon icon-primary"><use xlink:href="' + process.env.BASE_URL + 'svg/sprite.svg#it-download"></use></svg><span class="ml-2">' + documento.title + '</span>\
                    </a>\
                </div > '
    }
}


